<template>
  <div class="content">
      <van-nav-bar
        title=""
        left-text="去登录"
        right-text="首页"
        left-arrow
        @click-left="goLogin"
        @click-right="goHome"
      />
      <div class="row-text title">资料提交成功！</div>
      <div class="row-text">
        <p>1.请向您的推荐人索要并添加平台客服的微信</p>
        <p>2.客服通过好友后，请发送“注册手机号+审核”进行审核</p>
        <p>3.请扫下方二维码关注公众号。<br>（建议置顶，方便后续做任务）</p>
      </div>
     <img :src="pub_code" />
  </div>
</template>
<script>
import {
  Row,
  Col,
  CellGroup,
  Cell,
} from "vant";
import { COMMON_CONF } from "@/api/user";
export default {
  name: "follow",
  components: {
    [Row.name]: Row,
  },
  created() {
    this.initPage()
  },
  data() {
    return {
      pub_code:''
    }
  },
  methods: {
    initPage(){
      this.$reqGet(COMMON_CONF)
        .then(res => {
            let data = res.data.data;
            this.pub_code = data.pub_code
        }).catch((err) => {
        })
    },
    goLogin(){
        this.$router.push({name: 'login'});
    },
    goHome(){
        this.$router.push({name: 'index'});
    }
  }
}
</script>

<style lang="scss" scoped>
.row-text{
  padding-top: 15px;
  color: #12A9FA;
  font-size: 18px;
}
.title{
  font-size: 22px;
  font-weight: bold;
}
.content{
    text-align: center;
    background-color: #fff;
}
</style>


